import {
	mapGetters,
	mapActions,
	mapState,
	mapMutations
} from "vuex";
export default{
	data(){
		return{
			isShow:true,
			showUser:true,
			showBack:false
		}
	},
	props:['navText'],
	created() {
		
		var pages = getCurrentPages();
		var page = pages[pages.length - 1];
		var route = page.route;
		if(route == "pages/user/user"){
			this.showUser = false;
			//this.isShow= false;
		}
		if(route == "pages/login/login"){
			this.showBack = true
		}
		if(route == "pages/index/index"){
			this.isShow = false;
		}
		if (pages.length > 1){
			this.isShow= true;
			this.showUser = false;
			this.showBack = true
		}else{
			this.isShow = false;
		} 
		console.log(route)
	},
	computed: {
		...mapState(['hasLogin']),
	},
	methods:{
		...mapMutations(['LOGIN']),
		gologin(){
			let token =uni.getStorageSync('token');
			if(!token){
				uni.navigateTo({
					url:'/pages/login/login'
				})
				
			}
		},
		gouser(){
			uni.navigateTo({
				url:'/pages/user/user'
			})
		},
		goback(){
			uni.navigateBack()
		},
		gohome(){
			uni.switchTab({
				url:'/pages/index/index'
			})
		}
	}
}
